<template>
  <div id="App">
    <div class="display" v-if="text.length > 0">{{currWord || "Waiting"}}</div>
    <textarea class="input--text" placeholder="Paste text.." v-model="text"></textarea>
    <input class="input--speed" type="range" min="100" max="500" step="25" v-model="speed" />
    <button v-if="!isRunning" class="btn btn--start" @click="startApp">Start</button>
    <button v-else class="btn btn--stop" @click="stopApp">Stop</button>
    <button class="btn btn--reset" @click="resetApp">Reset</button>
  </div>
</template>

<script>
export default {
  name: "App",
  data: function() {
    return {
      text: "",
      currWordIndex: 0,
      isRunning: false,
      timeout: null,
      speed: 300
    };
  },
  computed: {
    arrText() {
      return this.text.split(" ");
    },
    currWord() {
      return this.arrText[this.currWordIndex];
    },
    betterSpeed() {
      if (this.arrText[this.currWordIndex].includes(".")) {
        return (
          (1000 / (this.speed / 60)) *
          Math.sqrt(this.arrText[this.currWordIndex].length)
        );
      } else {
        return (
          (1000 / (this.speed / 60)) *
          Math.sqrt(this.arrText[this.currWordIndex].length) *
          0.5
        );
      }
    }
  },
  methods: {
    startApp() {
      this.isRunning = true;
    },
    stopApp() {
      this.isRunning = false;
      clearTimeout(this.timeout);
    },
    resetApp() {
      this.isRunning = false;
      this.currWordIndex = 0;

      clearTimeout(this.timeout);
    },
    increaseCurrWordIndex() {
      this.currWordIndex++;

      if (this.currWordIndex == this.arrText.length) {
        this.resetApp();
      }
    },
    timeoutHandler() {
      if (this.isRunning) {
        this.increaseCurrWordIndex();
        this.timeout = setTimeout(this.timeoutHandler, this.betterSpeed);
      }
    }
  },
  watch: {
    isRunning() {
      if (this.isRunning) {
        this.increaseCurrWordIndex();
        this.timeoutHandler();
      } else {
        this.stopApp();
      }
    }
  }
};
</script>

<style>
@import url("https://fonts.googleapis.com/css?family=Open+Sans:300,400,600,700&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html,
body {
  position: relative;
  width: 100%;
  height: 100%;
  font-family: "Open Sans";
}

label,
input {
  display: block;
}

body {
  display: flex;
  justify-content: center;
  align-items: center;
  background: linear-gradient(to top, #272838, #5e5e72);
  flex-direction: column;
}

.btn {
  width: 22.5vmin;
  height: 8vmin;
  border: none;
  transition: all 300ms ease-in-out;
  background: #f9f8f8;
  font-size: 2vmin;
  color: #333333ee;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.2vmin;
  cursor: pointer;
}

.btn:hover {
  text-decoration: underline;
}

.btn:focus {
  outline: none;
}

.btn:active {
  transform: scale(0.9);
}

.btn--start,
.btn--stop {
  margin-right: 5vmin;
}

.display,
.input--text,
.btn {
  border-radius: 1vmin;
  box-shadow: 0 4px 6px #22222244;
}

::placeholder {
  font-size: 2.5vmin;
  color: #333333dd;
}

.display {
  height: 15vmin;
  width: 50vmin;
  background: #f9f8f8;
  margin-bottom: 5vmin;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 5vmin;
  color: #333333dd;
  position: relative;
}

.input--text {
  font-size: 2.5vmin;
  line-height: 3vmin;
  color: #333333ee;
  height: 30vmin;
  width: 50vmin;
  resize: none;
  border: 2px solid transparent;
  padding: 2vmin 2vmin;
  margin-bottom: 5vmin;
  background: #f9f8f8;
  opacity: 0.7;
}

.input--text:focus {
  outline: none;
  border: 2px solid #272838;
  border-radius: 1vmin;
}

.input--speed {
  width: 50vmin;
  margin-bottom: 5vmin;
}
</style>
